<template>
	<div id="detailPag">
		<div id='detailHeader'>
			<span class="header-item"></span>
			<div class="middle">
				<img src="./../img/logo.png" />
				<span class="city"></span>
			</div>
			<span class='toLogin'></span>
			<span class="search">&#xe651;</span>
			<ul class="logined-panel">
				<li></li>
				<li></li>
				<li></li>
			</ul>
		</div>
		<div class="detailContent">
			<div class="searchBtn">
				<input class="searchInput" type="text" placeholder=" / " />
				<span @click="searchRes()"></span>
			</div>
			
			<div class="swiper-container" id="swiper">
				<div class="swiper-wrapper">
					<div class="swiper-slide" v-for="(img,index) in product_images" :key="index">
						<img :src="img.img_url" />
					</div>
				</div>
				<div class="swiper-pagination"></div>
			</div>
			<div id='info'>
                <p class="title">{{basic.short_name}}-{{basic.spec}}</p>
                <span  class="heart" @click="islove()">&#xe629;</span>
                <p class='desc'>{{basic.description}}</p>
                <p class="price">
                    <span class="r-price">{{basic.price/100}} /{{basic.show_entity_name}}</span>
                    <span class="o-price">{{basic.origin_price/100}}</span>
                    <span class="status">
                        <span class="gap"></span>
                        <span></span>
                    </span>
                </p>
                <div class='tags'>
                    <span></span>
                </div>
            </div>
            <div class="panel-gap"></div>
            
            
            <div id="">
            	
            </div>
            
		</div>
	</div>
</template>

<script>
	import MyAjax from './../md/MyAjax.js';
	export default {
		data() {
			return {
				basic: [],
				product_images: [],
				modules: []
			}
		},
		updated() {
			var swiper = new Swiper('.swiper-container', {
				pagination: '.swiper-pagination',
				paginationClickable: true,
				loop: true,
				autoplay: 2000
			});
		},
		mounted() {
			var that = this;
			var url = 'https://api.ricebook.com/product/info/product_detail.json?product_id=1008749';
			MyAjax.fetch(url, function(res) {
				console.log(res)
				that.basic = res.basic;
				that.product_images = res.basic.product_images;
				that.modules = res.modules;
				console.log(that.basic, that.modules)
			}, function(err) {
				console.log(err)
			})
		}
	}
</script>

<style>

</style>


